* {
  box-sizing: border-box;
}
html {
  overflow-x: hidden;
  width: 100%;
}
:root {
  --brand-primary: linear-gradient(135deg, #9d8adb 0%, #4c4172 100%);
  --brand-secondary: #9d8adb;
  --brand-tertiary: #4c4172;
  --accent-primary: #c5a6f9;
  --accent-secondary: #ffffff;
  --neu-shadow-light: rgba(255, 255, 255, 0.6);
  --neu-shadow-dark: rgba(0, 0, 0, 0.25);
  --bg-image: url('/img/bg-img.png');
}
body {
  font-family: 'Inter', sans-serif;
  background: var(--brand-primary);
  color: #fff;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  overflow-x: hidden;
  animation: gradientMove 30s ease infinite;
  width: 100%;
  max-width: 100vw;
  box-sizing: border-box;
  background: var(--brand-primary);
  background-image: var(--bg-image);
  background-size: 200% 200%;
}
@keyframes gradientMove {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.neu-card {
  background: var(--brand-primary);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 8px 8px 16px var(--neu-shadow-dark),
              -8px -8px 16px var(--neu-shadow-dark);
  backdrop-filter: blur(12px);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.neu-card:hover {
  transform: translateY(-4px);
  box-shadow: 4px 4px 8px var(--neu-shadow-dark),
              -4px -4px 8px var(--neu-shadow-dark);
}
.neu-input {
  width: 100%;
  padding: 0.75rem 1rem;
  border-radius: 12px;
  border: 2px solid var(--accent-primary);
  background: #fdfdfd;
  color: var(--brand-tertiary);
  box-shadow: inset 4px 4px 8px var(--neu-shadow-dark),
              inset -4px -4px 8px var(--neu-shadow-dark);
  transition: all 0.2s ease;
}
.neu-input:focus {
  outline: none;
  border-color: var(--brand-secondary);
  box-shadow: 0 0 6px var(--brand-tertiary);
}
.neu-btn {
  display: inline-block;
  width: 100%;
  padding: 0.9rem;
  border-radius: 14px;
  font-weight: 700;
  color: var(--accent-secondary);
  background: var(--brand-secondary);
  border: none;
  cursor: pointer;
  box-shadow: 6px 6px 12px var(--neu-shadow-dark),
              -6px -6px 12px var(--neu-shadow-dark);
  transition: all 0.2s ease;
}
.neu-btn:hover {
  color: var(--brand-tertiary);
  background: var(--accent-primary);
  box-shadow: 3px 3px 6px var(--neu-shadow-dark),
              -3px -3px 6px var(--neu-shadow-dark);
}
.neu-btn:active {
  transform: translateY(2px);
  box-shadow: inset 4px 4px 8px var(--neu-shadow-dark),
              inset -4px -4px 8px var(--neu-shadow-dark);
}
.btn-back {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.6rem 1.2rem;
  border-radius: 9999px;
  background: var(--brand-primary);
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 6px 6px 12px var(--neu-shadow-dark),
    -4px -4px 10px var(--neu-shadow-dark);
  transition: all 0.2s ease-in-out;
}
.btn-back:hover {
  box-shadow: 4px 4px 8px var(--neu-shadow-dark),
    -4px -4px 8px var(--neu-shadow-dark);
  transform: translateY(-2px);
}
.btn-back:active {
  box-shadow: inset 3px 3px 6px var(--neu-shadow-dark),
              inset -3px -3px 6px var(--neu-shadow-dark);
  transform: translateY(0);
}
.neu-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 2rem;
  background: var(--brand-primary);
  box-shadow: 8px 8px 16px var(--neu-shadow-dark),
              -8px -8px 16px var(--neu-shadow-dark);
  backdrop-filter: blur(8px);
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 100vw;
  box-sizing: border-box;
  z-index: 50;
}
.neu-navbar-logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
}
.neu-navbar-links {
  display: flex;
  gap: 2rem;
}
.neu-navbar-links a {
  font-size: 1rem;
  font-weight: 500;
  color: #f1f1f1;
  text-decoration: none;
  transition: color 0.2s ease, transform 0.2s ease;
}
.neu-navbar-links a:hover {
  color: var(--pastel-green);
  transform: translateY(-2px);
}
.neu-navbar-actions {
  display: flex;
  gap: 1rem;
}
.neu-navbar-actions .btn-login,
.neu-navbar-actions .btn-signup {
  padding: 0.5rem 1rem;
  border-radius: 999px;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: all 0.2s ease-in-out;
}
.btn-login {
  background: var(--brand-secondary);
  color: #fff;
  box-shadow: 4px 4px 8px var(--neu-shadow-dark),
              -4px -4px 8px var(--neu-shadow-dark);
}
.btn-login:hover {
  background: var(--accent-primary);
  color: var(--brand-tertiary);
  transform: translateY(-2px);
}
.btn-signup {
  background: var(--brand-secondary);
  color: #fff;
  box-shadow: 4px 4px 8px var(--neu-shadow-dark),
              -4px -4px 8px var(--neu-shadow-dark);
}
.btn-signup:hover {
  background: var(--brand-secondary);
  color: var(--brand-tertiary);
  transform: translateY(-2px);
}
.btn-get-started {
  display: inline-block;
  padding: 1rem 2.5rem;
  border-radius: 9999px;
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--accent-secondary);
  background: var(--brand-secondary);
  cursor: pointer;
  text-align: center;
  transition: all 0.25s ease-in-out;
  box-shadow: 6px 6px 12px var(--neu-shadow-dark),
    -6px -6px 12px var(--neu-shadow-dark);
}
.btn-get-started:hover {
  color: var(--brand-tertiary);
  background: var(--accent-primary);
  transform: translateY(-2px);
  box-shadow: 3px 3px 6px var(--neu-shadow-dark),
    -3px -3px 6px var(--neu-shadow-dark);
}
.btn-get-started:active {
  transform: translateY(0);
  box-shadow: inset 4px 4px 8px var(--neu-shadow-dark),
              inset -4px -4px 8px var(--neu-shadow-dark);
}
.heading-1 {
  font-size: 3rem;
  font-weight: 900;
  line-height: 1.1;
  color: #fff;
  margin-bottom: 0.5rem;
}
.heading-2 {
  font-size: 2.5rem;
  font-weight: 800;
  line-height: 1.2;
  color: var(--brand-tertiary);
  margin-bottom: 1rem;
}
.heading-3 {
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.3;
  color: var(--brand-secondary);
  margin-bottom: 0.5rem;
}
.heading-4 {
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 1.4;
  color: var(--brand-secondary);
  margin-bottom: 0.5rem;
}
.heading-5 {
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.4;
  color: var(--accent-primary);
  margin-bottom: 0.5rem;
}
.heading-6 {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.5;
  color: var(--brand-tertiary);
  margin-bottom: 0.5rem;
}
.paragraph {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.6;
  color: #fff;
  margin-bottom: 1rem;
}
.paragraph-small {
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5;
  color: #555;
}
.success-msg {
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.6;
  color: rgba(0, 128, 0, 0.500);
}
.error-msg {
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.6;
  color: rgba(255, 0, 0, 0.500);
}
.sidebar {
  width: 250px;
  background: var(--brand-tertiary);
  padding: 30px;
  display: flex;
  flex-direction: column;
  border-radius: 0 18px 18px 0;
  box-shadow: inset 4px 4px 10px var(--neu-shadow-dark),
              inset -4px -4px 10px var(--neu-shadow-dark);
  position: sticky;
  top: 0;
  left: 0;
  height: 105vh;
  overflow-y: visible;
}
.logo {
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 8px;
}
.subtitle {
  font-size: 0.85rem;
  color: var(--brand-secondary);
  margin-bottom: 20px;
}
.welcome {
  font-size: 1rem;
  margin: 14px 0;
}
.welcome span {
  font-size: 1rem;
  font-weight: 700;
  color: #fff;
}
.menu {
  list-style: none;
  margin-top: 10px;
}
.menu li {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 16px 0;
  padding: 10px 14px;
  border-radius: 12px;
  background: var(--brand-tertiary);
  box-shadow: inset 4px 4px 10px var(--neu-shadow-dark),
              inset -4px -4px 10px var(--neu-shadow-dark);
  cursor: pointer;
  transition: 0.2s ease;
}
.menu li:hover {
  background: var(--brand-secondary);
  color: var(--brand-tertiary);
  transform: translateY(-2px);
}
.main-content {
  flex: 1;
  padding: 30px;
  background: var(--brand-secondary);
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.top-row, .middle-row, .bottom-row {
  display: flex;
  gap: 20px;
}
.card {
  flex: 1;
  background: var(--brand-tertiary);
  border-radius: 20px;
  padding: 20px;
  box-shadow: inset 4px 4px 10px var(--neu-shadow-dark),
              inset -4px -4px 10px var(--neu-shadow-dark);
  text-align: center;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.card:hover {
  transform: translateY(-3px);
  box-shadow: 2px 2px 8px var(--neu-shadow-dark),
              -2px -2px 8px var(--neu-shadow-dark);
}
.quiz .highlight, .review .highlight {
  font-size: 2rem;
  font-weight: bold;
  color: var(--accent-primary);
}
.slide-box {
  margin-top: 15px;
  height: 120px;
  border-radius: 15px;
  background: var(--brand-tertiary);
  box-shadow: inset 4px 4px 10px var(--neu-shadow-dark),
              inset -4px -4px 10px var(--neu-shadow-dark);
}
.more-menu {
  position: relative;
}
.more-dropdown {
  position: absolute;
  top: 40px;
  left: 40px;
  background: var(--accent-secondary);
  border-radius: 12px;
  margin-top: 6px;
  padding: 8px;
  width: 200px;
  display: none;
  flex-direction: column;
  z-index: 100;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
}
.more-menu:hover .more-dropdown {
  display: flex;
}
.more-dropdown li {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 12px;
  margin: 2px 0;
  cursor: pointer;
  transition: 0.2s ease;
  color: #fff;
}
.more-dropdown li:hover {
  background: var(--brand-secondary);
  color: var(--brand-tertiary);
}
@media (min-width: 1440px) {
  .container {
    max-width: 1920px;
  }
}
@media (max-width: 1439px) {
  .neu-navbar {
    padding: 1rem 1.5rem;
  }
  .heading-1 {
    font-size: 2.5rem;
  }
}
@media (max-width: 1023px) {
  .neu-navbar {
    padding: 1rem;
    flex-wrap: wrap;
  }
  .neu-navbar-logo {
    font-size: 1.25rem;
    gap: 0.5rem;
  }
  .neu-navbar-logo img {
    height: 2rem;
    width: 2rem;
  }
  .neu-navbar-logo div span:first-child {
    font-size: 1.5rem;
  }
  .neu-navbar-logo div span:last-child {
    font-size: 0.75rem;
  }
  .neu-navbar-actions {
    gap: 0.75rem;
  }
